.form {
	&__wrap {
		display: flex;
		justify-content: space-between;
		gap: 3rem;
		@media screen and (max-width: 576px) {
			display: block;
		}
	}

	&__wrap .form__group {
		flex: 1;
	}

	&__group {
		margin-bottom: 2.4rem;

		.form__label,
		label {
			margin-bottom: 1.2rem;
			display: block;
			font-size: 1.6rem;
			color: white;
		}
	}

	input[type="text"],
	input[type="email"],
	input[type="tel"],
	textarea {
		height: 5.2rem;
		width: 100%;
		font-family: var(--font-1);
		font-size: 1.6rem;
		text-indent: 1.6rem;
		border-radius: 4px;
		border: 1px solid rgba(255, 255, 255, 0.2);
		background: rgba(255, 255, 255, 0.12);
		color: white;
	}

	textarea {
		height: 12.8rem;
		resize: vertical;
	}

	input[type="text"]::placeholder,
	input[type="email"]::placeholder,
	input[type="tel"]::placeholder,
	textarea::placeholder {
		color: rgba(255, 255, 255, 0.6);
	}

	&__footer {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-top: 4.8rem;
		@media screen and (max-width: 576px) {
			display: block;
		}
	}

	&__checkbox {
		display: none;
	}

	&__checkbox:checked + label:after {
		opacity: 1;
	}
  &__privacy {
    @media screen and (max-width: 576px) {
      margin-bottom: 3.2rem;
    }
  }

	&__privacy label {
		padding-left: 2.8rem;
		padding-right: 6.5rem;
		display: block;
		position: relative;
		font-size: 1.6rem;
		line-height: 1.6;
		cursor: pointer;
	}

	&__privacy label::before {
		content: "";
		position: absolute;
		top: 0.7rem;
		left: 0;
		width: 1.6rem;
		height: 1.6rem;
		background: var(--accent);
		border-radius: 4px;
	}

	&__privacy label::after {
		content: "";
		position: absolute;
		top: 1.1rem;
		left: 0.2rem;
		width: 1rem;
		height: 0.8rem;
		background: url("../img/freeseo/form-check.svg") no-repeat center;
		opacity: 0;
		transition: all 0.4s;
	}

	&__submit {
		flex: 0 0 22.4rem;
	}

	&__submit:disabled {
		opacity: 0.6;
		cursor: not-allowed;
	}

	select {
		display: none;
	}

	&__file {
		display: flex;
		align-items: center;
		position: relative;
		height: 3rem;
		font-family: var(--font-1);
		font-size: 1.6rem;
		color: var(--text-light);

		svg {
			margin-right: 1.2rem;
		}

		input {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: 10;
			opacity: 0;
			cursor: pointer;
		}
	}

	&.form--light {
		.form__group {
			.form__label,
			label {
				color: var(--text-light);
			}
		}

		input[type="text"],
		input[type="email"],
		textarea {
			border-radius: 4px;
			border: 1px solid var(--gray-400, #d7dadd);
			background: var(--gray-200, #f4f5f6);

			&::placeholder {
				color: #9a9ca5;
			}
		}

		.form__footer {
			margin-top: 3.4rem;
			display: block;
			&.form__footer--flex {
				display: flex;
				align-items: flex-start;
			}
		}

		.form__privacy {
			margin-bottom: 4rem;
		}

		.form__privacy label {
			&::before {
				background: transparent;
				border: 1px solid var(--accent);
			}

			&::after {
				left: 0.4rem;
				width: 1rem;
				height: 1rem;
				background: var(--accent);
			}
		}
	}

	&__radio {
		display: flex;
		align-items: center;

		input {
			display: none;

			&:checked + label {
				color: white !important;
				background: var(--accent);
			}
		}

		label {
			position: relative;
			display: flex;
			justify-content: center;
			align-items: center;
			margin-right: 1.2rem;
			margin-bottom: 1.2rem;
			height: 3.2rem;
			font-size: 1.4rem;
			font-weight: 700;
			padding: 0 1.6rem;
			border-radius: 4px;
			border: 1px solid var(--gray-400, #d7dadd);
			transition: all 0.4s;
			cursor: pointer;

			&:hover {
				color: white !important;
				background: var(--accent);
			}
		}
	}
}
